"use client"

import { PieChart, Pie, Cell, ResponsiveContainer, Tooltip, Legend } from "recharts"
import { CHART_COLORS } from "@/lib/chart-colors"
import type { Attendee } from "@/lib/parse-csv"

interface Props {
  attendees: Attendee[]
}

export function PessoasTransporteChart({ attendees }: Props) {
  const counts: Record<string, number> = {}

  attendees.forEach((a) => {
    const pessoas = a.pessoasTransporte || "Não informado"
    if (pessoas && pessoas !== "") {
      counts[pessoas] = (counts[pessoas] || 0) + 1
    }
  })

  const data = Object.entries(counts)
    .filter(([name]) => name !== "Não informado")
    .map(([name, value]) => ({
      name: name === "1" ? "Sozinho" : `${name} pessoas`,
      value,
    }))
    .sort((a, b) => b.value - a.value)
    .slice(0, 6)

  return (
    <div className="bg-white/10 backdrop-blur-sm border border-white/20 rounded-xl p-5">
      <h3 className="text-lg font-semibold text-white mb-4">Pessoas por Transporte</h3>
      <div className="h-[300px]">
        <ResponsiveContainer width="100%" height="100%">
          <PieChart>
            <Pie
              data={data}
              cx="50%"
              cy="50%"
              outerRadius={90}
              dataKey="value"
              label={({ percent }) => `${(percent * 100).toFixed(0)}%`}
            >
              {data.map((_, index) => (
                <Cell key={`cell-${index}`} fill={CHART_COLORS[index % CHART_COLORS.length]} />
              ))}
            </Pie>
            <Tooltip
              contentStyle={{
                backgroundColor: "rgba(30, 60, 90, 0.95)",
                border: "1px solid rgba(255,255,255,0.2)",
                borderRadius: "8px",
                color: "#fff",
              }}
              formatter={(value: number) => [`${value}`, "Valor"]}
            />
            <Legend formatter={(value) => <span style={{ color: "#fff" }}>{value}</span>} />
          </PieChart>
        </ResponsiveContainer>
      </div>
    </div>
  )
}
